<script>
import { mapGetters } from "vuex";
export default {
  name: "navbar",
  data() {
    return {
      auth: "",
    };
  },
  computed: {
    categories() {
      return this.$store.state.categories;
    },
    ...mapGetters({
      cartItems: "get_cart",
      count: "getCartLength",
    }),
    CurrentUser() {
      return this.$store.state.CurrentUser;
    },
    SearchFor: {
      get() {
        return this.$store.state.searchFor;
      },
      set(value) {
        this.$store.commit("getSearchFor", value);
      },
    },
  },
  methods: {
    getCat: function () {
      this.$root.$refs.A.getCategory();
    },
    user() {
      this.$store.dispatch("getUser");
    },
    logout() {
      this.$store.dispatch("getLogout");
    },
    getSearch() {
      this.$store.dispatch("getResults");
    },
    openNav() {
      document.getElementById("mySidebar").style.width = "350px";
      // document.getElementById("app").style.marginLeft = "350px";
    },
    closeNav() {
      document.getElementById("mySidebar").style.width = "0";
      // document.getElementById("app").style.marginLeft = "0";
    },
  },
  created() {
    fetch("http://vuengo.herokuapp.com/auth_config/").then((result) => {
      return result.json().then((data) => {
        localStorage.setItem("isAuth", data);
        this.auth = data;
      });
    });
    console.log(localStorage.isAuth);
    this.user();
  },
};
</script>

