<template>
  <div id="app">
    <Navbar v-if="!['login', 'signup', '404'].includes($route.name)" />
    <transition name="slide" mode="out-in">
      <router-view
        ><VueSkeletonLoader
          type="circle"
          :width="200"
          :height="200"
          animation="fade"
      /></router-view>
      /</transition
    >
    <Footer v-if="!['login', 'signup', '404'].includes($route.name)" />
  </div>
</template>

<script>
import Navbar from "./components/navbar.vue";
import Footer from "./components/footer.vue";
import VueSkeletonLoader from "skeleton-loader-vue";
export default {
  name: "App",
  components: {
    Navbar,
    Footer,
    VueSkeletonLoader,
  },
  methods: {
    user() {
      this.$store.dispatch("getUser");
    },
    cart() {
      this.$store.dispatch("getCart");
    },
  },
  created() {
    fetch("http://vuengo.herokuapp.com/auth_config/").then((result) => {
      return result.json().then((data) => {
        localStorage.setItem("isAuth", data);
      });
    });
    this.cart();
    this.user();
  },
};
</script>
