<template>
  <div class="card user-card border-0 container">
    <div class="card-body text-center">
      <div class="media">
        <div class="media-body ml-3 pt-4">
          <h4>{{ first_name }} {{ last_name }}</h4>
          <div class="small text-muted">Joined Dec 31, 2017</div>
        </div>
      </div>
      <hr />
      <div class="row mt-5">
        <ul class="d-inline nav-pills col-md-3">
          <li
            class="nav-item"
            @click="activate(1)"
            :class="{ active: active_el == 1 }"
          >
            <button class="btn-profile border-0" @click="hidden = false">
              Profile
            </button>
          </li>
          <li
            class="nav-item"
            @click="activate(2)"
            :class="{ active: active_el == 2 }"
          >
            <button class="btn-profile border-0">Orders</button>
          </li>
          <li
            class="nav-item"
            @click="activate(3)"
            :class="{ active: active_el == 3 }"
          >
            <button class="btn-profile border-0">Address</button>
          </li>
          <li
            class="nav-item"
            @click="activate(4)"
            :class="{ active: active_el == 4 }"
          >
            <button class="btn-profile border-0" @click="hidden = true">
              Change Password
            </button>
          </li>
        </ul>
        <form
          @submit.prevent="update_profile"
          method="post"
          class="col-md-7 mx-auto profile-style"
          v-if="!hidden"
        >
          <h2 class="mb-3">Profile</h2>
          <div class="form-row">
            <div class="form-group col-sm-6">
              <label for="profileFirstName">First Name</label>
              <input
                type="text"
                class="profile w-100"
                id="profileFirstName"
                required=""
                v-model="first_name"
              />
            </div>
            <div class="form-group col-sm-6">
              <label for="profileLastName">Last Name</label>
              <input
                type="text"
                class="profile w-100"
                id="profileLastName"
                required=""
                v-model="last_name"
              />
            </div>
            <div class="form-group col-sm-12">
              <label for="profileEmail">Username</label>
              <input
                type="text"
                class="profile w-100"
                id="profileEmail"
                required=""
                v-model="username"
              />
            </div>
            <div class="form-group col-sm-12">
              <label for="profileEmail">Email address</label>
              <input
                type="email"
                class="profile w-100"
                id="profileEmail"
                required=""
                v-model="email"
              />
            </div>
            <div class="form-group col-12">
              <button type="submit" class="btn btn-success btn-block mt-3">
                SAVE
              </button>
            </div>
          </div>
        </form>
        <div
          class="col-md-7 mx-auto profile-style"
          v-if="hidden"
          data-active="change_password"
        >
          <h2>Change Password</h2>
          <form @submit.prevent="changePassword" method="post">
            <div class="form-row">
              <div class="form-group col-sm-12">
                <label for="profilePassword">Old Password</label>
                <input
                  type="password"
                  :class="v_Oldpassword ? 'invalid' : null"
                  class="profile w-100"
                  id="profilePassword"
                  v-model="oldPassword"
                />
                <div
                  class="invalidtooltip"
                  :class="display"
                  v-if="v_Oldpassword"
                >
                  {{ v_Oldpassword.old_password }}
                </div>
              </div>
              <div class="form-group col-sm-12">
                <label for="profilePassword">New Password</label>
                <input
                  type="password"
                  class="profile w-100"
                  id="profilePassword"
                  v-model="newPassword1"
                  :class="vNewPassword ? 'invalid' : null"
                />
                <div
                  class="invalidtooltip"
                  :class="display"
                  v-if="vNewPassword"
                >
                  {{ vNewPassword[0] }}
                </div>
              </div>
              <div class="form-group col-sm-12">
                <label for="profileConfirmPassword">Confirm Password</label>
                <input
                  type="password"
                  class="profile w-100"
                  :class="vNewPassword ? 'invalid' : null"
                  id="profileConfirmPassword"
                  v-model="newPassword2"
                />
                <div
                  class="invalidtooltip"
                  :class="display"
                  v-if="v_duplicatePassword"
                >
                  {{ v_duplicatePassword[0] }}
                </div>
              </div>
              <div class="form-group col-12">
                <button type="submit" class="btn btn-success btn-block mt-3">
                  SAVE
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "profile",
  data() {
    return {
      active_el: 1,
      hidden: false,
      oldPassword: "",
      newPassword1: "",
      newPassword2: "",
      Wvalid: "",
      display: "",
      vOldPassword: null,
      vNewPassword: null,
      noneFields: null,
    };
  },
  computed: {
    pk() {
      return this.$store.state.UserId;
    },
    first_name: {
      get() {
        return this.$store.state.first_name;
      },
      set(value) {
        this.$store.commit("updateFirstName", value);
      },
    },
    last_name: {
      get() {
        return this.$store.state.last_name;
      },
      set(value) {
        this.$store.commit("updateLastName", value);
      },
    },
    username: {
      get() {
        return this.$store.state.CurrentUser;
      },
      set(value) {
        this.$store.commit("updateUserName", value);
      },
    },
    email: {
      get() {
        return this.$store.state.email;
      },
      set(value) {
        this.$store.commit("updateEmail", value);
      },
    },
    v_Oldpassword() {
      return this.vOldPassword;
    },
    v_NewPassword() {
      return this.vNewPassword;
    },
    v_duplicatePassword() {
      return this.noneFields;
    },
  },
  methods: {
    update_profile() {
      axios
        .put("http://vuengo.herokuapp.com/update_profile/" + this.pk + "/", {
          csrfmiddlewaretoken: window.$cookies.get("csrftoken"),
          username: this.username,
          email: this.email,
          first_name: this.first_name,
          last_name: this.last_name,
        })
        .then((response) => {
          console.log(response.data);
          this.$router.push({ name: "profile" });
        })
        .catch((error) => {
          console.log(error);
        });
    },
    changePassword() {
      axios
        .put("http://vuengo.herokuapp.com/change_password/" + this.pk + "/", {
          csrfmiddlewaretoken: window.$cookies.get("csrftoken"),
          old_password: this.oldPassword,
          password: this.newPassword1,
          password2: this.newPassword2,
        })
        .then((response) => {
          console.log(response.data);
          this.$router.push({ name: "Home" });
        })
        .catch((error) => {
          this.Wvalid = "";
          console.log(error.response.data);
          console.log(error.response.data.old_password);
          this.vOldPassword = error.response.data.old_password;
          this.vNewPassword = error.response.data.password;
          this.noneFields = error.response.data.non_field_errors;
          this.display = "d-block";
          this.Wvalid = "invalid";
        });
    },
    isDisable(...items) {
      items.forEach((item) => {
        console.log(item.length);
        return item.length == 0;
      });
    },
    activate(el) {
      this.active_el = el;
    },
  },
};
</script>
