<template>
  <b-container style="margin-bottom: 8rem">
    <h1 class="text-left mt-5 mr-1 align-items-end">
      Cart<b-badge variant="danger" style="font-size: 14px" pill>{{
        count
      }}</b-badge>
    </h1>
    <hr />
    <b-list-group>
      <b-col sm="auto">
        <b-list-group-item
          class="d-flex justify-content-between align-items-center mt-3"
          v-for="(item, index) in cart"
          :key="index"
        >
          <img
            :src="item.image"
            img-top
            alt="Image"
            class="rounded-0"
            style="width: 4rem"
          />
          <div
            class="d-flex flex-column w-100 align-items-start justify-content-between text-left"
            style="margin-left: 2.5rem"
          >
            <h6 class="mb-1 text-secondary">{{ item.name }}</h6>
            <small class="text-success mt-3"
              >Quantity: {{ item.quantity }}</small
            >
          </div>
          <b-badge variant="warning" pill>{{
            +item.price.slice(1).replace(",", "") * item.quantity
          }}</b-badge>
          <button
            class="btn btn-secondary btn-sm ml-4 rounded-circle"
            @click="remove(index, item.id)"
          >
            X
          </button>
        </b-list-group-item>
      </b-col>
    </b-list-group>
    <hr />
    <div class="mb-5 clearfix">
      <button
        class="btn btn-secondary float-right block rounded ml-auto"
        disabled
      >
        Total: {{ getTotal }}
      </button>
      <button class="btn btn-success float-left block rounded" @click="buy()">
        <b-icon icon="chevron-double-left"></b-icon> Checkout
      </button>
    </div>
  </b-container>
</template>
<script>
import { mapGetters } from "vuex";
import axios from "axios";
export default {
  name: "AddToCart",
  methods: {
    buy() {
      fetch("http://vuengo.herokuapp.com//config/")
        .then(function (result) {
          return result.json();
        })
        .then(function (data) {
          // Initialize Stripe.js
          const stripe = window.Stripe(data.publicKey);
          console.log(stripe);
          fetch("http://vuengo.herokuapp.com//api/create-checkout-session/")
            .then(function (result) {
              return result.json();
            })
            .then(function (data) {
              console.log(data);
              // Redirect to Stripe Checkout
              return stripe.redirectToCheckout({ sessionId: data.sessionId });
            })
            .then(function (res) {
              console.log(res);
            });
        });
    },
    cart() {
      this.$store.dispatch("getCart");
    },
    remove(index, id) {
      this.$swal
        .fire({
          title: "Are You Sure?",
          showCancelButton: true,
          confirmButtonText: `Ok`,
        })
        .then((result) => {
          if (result.isConfirmed) {
            this.$delete(this.cart, index);
            axios
              .delete("http://vuengo.herokuapp.com/data/cart/" + id + "/")
              .then((response) => {
                this.cart();
                console.log(response);
              })
              .catch((error) => {
                console.log(error.response);
              });
          }
        });
    },
  },
  computed: {
    ...mapGetters({
      cart: "get_cart",
      count: "getCartLength",
    }),
    getTotal() {
      var total = 0;
      this.cart.forEach((item) => {
        total += +item.price.slice(1).replace(",", "");
      });
      return "$" + total.toFixed(2);
    },
  },
  created() {
    this.cart();
  },
};
</script>
<style lang="scss" scoped>
.btn-secondary {
  color: #fff;
  background-color: #e4e3e3;
  border-color: #ffffff;
}
.list-group-item {
  border: 1px solid #eee;
  border-radius: 10px;
}
.list-group-item + .list-group-item {
  border-top-width: thin;
}
</style>
