<template>
  <transition name="slide" mode="out-in">
    <b-container class="bv-example-row">
      <h3 class="top mt-5">{{ this.$route.params.slug }}</h3>
      <hr />
      <div class="row mt-5">
        <div
          class="col-md-4 col-lg-3 col-sm-12"
          v-for="product in category"
          :key="product.id"
        >
          <div class="profile-card-4 text-center">
            <router-link :to="'/products/' + product.id + '/' + product.slug">
              <img
                :src="product.image"
                img-top
                style="height: 14rem; width: 100%"
                class="img img-responsive"
                img-alt=""
                v-if="product.image"
              />
            </router-link>
            <div class="profile-content">
              <!-- <div class="profile-name">
                {{ product.name.slice(0, 100) }}
                <p>@johndoedesigner</p>
              </div> -->
              <div class="profile-description">
                {{ product.name.slice(0, 100) }}
              </div>
              <div class="row">
                <div class="col-xs-4">
                  <div class="profile-overview">
                    <p>Price</p>
                    <h4>{{ product.price }}</h4>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </b-container>
  </transition>
</template>
<script>
import axios from "axios";
export default {
  name: "shopByCategory",
  data() {
    return {
      category: [],
      products: [],
    };
  },
  created() {
    this.$root.$refs.A = this;
  },
  methods: {
    getCategory() {
      axios
        .get(
          `http://vuengo.herokuapp.com/data/products/?category=${this.categoryId}&min_price=&max_price=&featured=`
        )
        .then((res) => {
          this.category = [];
          for (const cat of res.data) {
            this.category.push(cat);
          }
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  computed: {
    categoryId() {
      return this.$route.params.id;
    },
  },
  watch: {
    item: {
      handler() {
        axios
          .get(
            `http://vuengo.herokuapp.com/data/products/?category=${this.categoryId}&min_price=&max_price=&featured=`
          )
          .then((res) => {
            for (const cat of res.data) {
              this.category.push(cat);
            }
          })
          .catch((e) => {
            console.log(e);
          });
      },
      deep: true,
      immediate: true,
    },
  },
};
</script>
