let CartUrl = "http://vuengo.herokuapp.com/data/cart/";

import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import router from "../router";
import { items, featuredProducts, AllCategories, AllUsers } from "../data.js";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    users: AllUsers,
    items: items,
    featuredProducts: featuredProducts,
    categories: AllCategories,
    cart: [],
    results: [],
    searchFor: "",
    isAuth: null,
    CurrentUser: "",
    UserId: "",
    first_name: "",
    last_name: "",
    email: "",
  },
  getters: {
    get_cart: (state) => state.cart,
    getCartLength: (state) => state.cart.length,
  },
  mutations: {
    Auth(state) {
      fetch("http://vuengo.herokuapp.com/auth_config/").then((result) => {
        return result.json().then((data) => {
          localStorage.setItem(state.isAuth, data);
          state.isAuth = data;
        });
      });
    },
    search(state) {
      axios
        .get("http://vuengo.herokuapp.com/search/?search=" + state.searchFor)
        .then((response) => {
          if (state.searchFor.length > 1) {
            state.results = [];
            for (const item of response.data) {
              state.results.push(item);
            }
            const path = `/search/` + state.searchFor;
            if (router.currentRoute.path !== path) {
              router.push(path);
            }
          } else {
            router.push("/");
          }
        })
        .catch((res) => {
          console.log(res);
        });
    },
    GetCart(state) {
      axios
        .get(CartUrl)
        .then((res) => {
          for (const item of res.data) {
            const Item = state.cart.find((x) => x.price === item.price);
            if (Item) {
              continue;
            } else {
              state.cart.push(item);
            }
          }
        })
        .catch((e) => {
          console.log(e);
        });
      return state.cart;
    },
    user(state) {
      axios
        .get("http://vuengo.herokuapp.com/auth/user/")
        .then((response) => {
          state.UserId = response.data.pk;
          state.CurrentUser = response.data.username;
          state.first_name = response.data.first_name;
          state.last_name = response.data.last_name;
          state.email = response.data.email;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    logout(state) {
      axios
        .get("http://vuengo.herokuapp.com/api/logout/")
        .then(() => {
          state.cart = [];
          fetch("/auth_config/").then((result) => {
            return result.json().then((data) => {
              localStorage.setItem("isAuth", data);
            });
          });
          state.isAuth = false;
          router.push({ name: "login" });
        })
        .catch((error) => {
          console.log(error);
        });
    },
    updateFirstName(state, first_name) {
      state.first_name = first_name;
    },
    updateLastName(state, last_name) {
      state.last_name = last_name;
    },
    updateUserName(state, username) {
      state.username = username;
    },
    updateEmail(state, email) {
      state.email = email;
    },
    getSearchFor(state, searchFor) {
      state.searchFor = searchFor;
    },
  },
  actions: {
    getUser(context) {
      return context.commit("user");
    },
    getLogout(context) {
      return context.commit("logout");
    },
    getCart(context) {
      return context.commit("GetCart");
    },
    getResults(context) {
      return context.commit("search");
    },
    getAuth(context) {
      return context.commit("Auth");
    },
  },
});
