<template>
  <b-container>
    <b-card no-body class="overflow-hidden border-0" style="margin-top: 5rem">
      <b-row no-gutters>
        <b-col md="6">
          <b-card-img
            :src="item.image"
            img-top
            alt="Image"
            class="rounded-0"
            style="width: 70%"
          ></b-card-img>
        </b-col>
        <b-col md="6">
          <b-card-body
            :title="item.name"
            style="line-height: 2rem"
            class="text-left"
          >
            <b-card-text>
              <p>
                Lorem, ipsum dolor sit amet consectetur adipisicing elit. Enim,
                voluptas deleniti! Ad sapiente nostrum, vero, saepe voluptatem
                eos iusto exercitationem ipsa nemo in recusandae expedita
                repudiandae assumenda maiores, quis quo?
              </p>
            </b-card-text>
            <b-list-group flush>
              <b-list-group-item class="text-warning"
                ><b-badge variant="warning" pill style="font-size: 100%">
                  {{ item.price }}</b-badge
                ></b-list-group-item
              >
              <b-list-group-item class="text-success border-0">
                <b-form inline>
                  <b-form-input
                    type="number"
                    id="quantity"
                    placeholder="Quantity"
                    size="sm"
                    :value="item.quantity"
                  ></b-form-input>
                  <label for="quantity" class="ml-4">Quantity</label>
                </b-form>
              </b-list-group-item>
              <b-list-group-item class="text-dark"
                ><p>
                  <b-badge variant="info" pill> {{ item.delivery }}</b-badge>
                </p>
                <p>
                  <b-badge variant="info" pill> {{ item.stock }}</b-badge>
                </p></b-list-group-item
              >
              <b-list-group-item class="text-dark mt-5">
                <button class="btn btn-secondary btn-block">Buy Now</button>
                <button
                  class="btn btn-success btn-block ml-sm-1"
                  @click="AddToCart()"
                >
                  Add To Cart
                </button>
              </b-list-group-item>
            </b-list-group>
          </b-card-body>
        </b-col>
      </b-row>
    </b-card>
  </b-container>
</template>
<script>
import axios from "axios";
export default {
  name: "productDetails",
  data() {
    return {
      item: [],
    };
  },
  computed: {
    productID() {
      return this.$route.params.id; // From route
    },
    UserId() {
      return this.$store.state.UserId;
    },
    items() {
      return this.$store.state.items;
    },
    cartItems() {
      return this.$store.state.cart;
    },
  },
  methods: {
    AddToCart() {
      if (this.$store.state.cart.find((x) => x.image === this.item.image)) {
        this.$swal("This Item Aready Exist.");
      } else {
        axios
          .post("http://vuengo.herokuapp.com/data/cart/", {
            csrfmiddlewaretoken: window.$cookies.get("csrftoken"),
            name: this.item.name,
            image: this.item.image,
            price: this.item.price,
            quantity: this.item.quantity,
            user: this.UserId,
          })
          .then((response) => {
            console.log(response.data);
            this.cart();
            this.$swal("Item Added Successfully.");
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    getItem() {
      this.items.forEach((e) => {
        if (e.id == this.productID) {
          this.item = e;
        }
      });
    },
    cart() {
      this.$store.dispatch("getCart");
    },
  },
  created() {
    this.getItem();
    this.cart();
  },
};
</script>
