<template>
  <section class="ftco-section">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-5 col-lg-5">
          <div class="profile p-5 mt-5">
            <h2>Signup</h2>
            <form @submit.prevent="register()" method="post">
              <div class="form-group">
                <input
                  type="text"
                  name="username"
                  class="profile w-100"
                  :class="v_username ? 'invalid' : null"
                  required=""
                  v-model="username"
                  placeholder="Username"
                />
                <div class="invalidtooltip" :class="display" v-if="v_username">
                  {{ v_username[0] }}
                </div>
              </div>
              <div class="form-group">
                <input
                  type="email"
                  name="email"
                  class="profile w-100"
                  :class="v_email ? 'invalid' : null"
                  required=""
                  v-model="email"
                  placeholder="Email"
                />
                <div class="invalidtooltip" :class="display" v-if="v_email">
                  {{ v_email[0] }}
                </div>
              </div>
              <div class="form-group">
                <input
                  type="password"
                  name="password"
                  class="profile w-100"
                  :class="v_password ? 'invalid' : null"
                  required=""
                  v-model="password"
                  placeholder="Password"
                />
                <div class="invalidtooltip" :class="display" v-if="v_password">
                  {{ v_password[0] }}
                </div>
              </div>
              <div class="form-group">
                <input
                  type="password"
                  name="password"
                  class="profile w-100"
                  :class="v_password2 ? 'invalid' : null"
                  required=""
                  v-model="password2"
                  placeholder="Confirm Password"
                />
                <div
                  class="invalidtooltip"
                  :class="display"
                  v-if="v_password2 || v_duplicatePassword"
                >
                  {{ v_password2[0] }} {{ v_duplicatePassword[0] }}
                </div>
              </div>
              <div class="form-group">
                <button
                  type="submit"
                  class="form-control btn btn-success submit px-3"
                >
                  Register
                </button>
              </div>
            </form>
            <p class="w-100 text-center mt-4">
              &mdash; Already have an account?
              <router-link to="login">Login</router-link> &mdash;
            </p>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import axios from "axios";
export default {
  name: "signup",
  data() {
    return {
      Wvalid: "",
      display: "",
      username: "",
      email: "",
      password: "",
      password2: "",
      vUserName: null,
      vEmail: null,
      vPassword: null,
      vPassword2: null,
      noneFields: null,
    };
  },
  computed: {
    v_username() {
      return this.vUserName;
    },
    v_email() {
      return this.vEmail;
    },
    v_password() {
      return this.vPassword;
    },
    v_password2() {
      return this.vPassword2;
    },
    v_duplicatePassword() {
      return this.noneFields;
    },
  },
  methods: {
    register() {
      axios
        .post("http://vuengo.herokuapp.com/auth/register/", {
          csrfmiddlewaretoken: window.$cookies.get("csrftoken"),
          username: this.username,
          email: this.email,
          password1: this.password,
          password2: this.password2,
        })
        .then((response) => {
          console.log(response.data);
          this.$router.push({ name: "login" });
        })
        .catch((error) => {
          this.Wvalid = "";
          console.log(error.response.data);
          this.vUserName = error.response.data.username;
          this.vEmail = error.response.data.email;
          this.vPassword = error.response.data.password1;
          this.vPassword2 = error.response.data.password2;
          this.noneFields = error.response.data.non_field_errors;
          this.display = "d-block";
          this.Wvalid = "invalid";
          console.log(this.v_username);
        });
    },
    checkForm(event) {
      event.preventDefault();
      event.target.classList.add("was-validated");
    },
  },
};
</script>
