import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import productList from "../components/Shop/productList";
import productDetails from "../components/Shop/productDetails";
import categoryList from "../components/Shop/categoryList";
import shopByCategory from "../components/Shop/shopByCategory";
import AddToCart from "../components/Shop/AddToCart";
import search from "../components/search";
import login from "../components/User/login";
import signup from "../components/User/signup";
import profile from "../components/User/profile";
import forgetPassword from "../components/User/forgetPassword";
import success from "../components/User/success";
import cancel from "../components/User/cancel";
import NotFound from "../components/404";

function guardLogin(to, from, next) {
  fetch("/auth_config/").then((result) => {
    return result.json().then((data) => {
      localStorage.setItem("isAuth", data);
    });
  });
  console.log(localStorage.isAuth);
  if (localStorage.isAuth == true) {
    if (to.name == "login") {
      next("/");
    } else {
      next();
    }
  } else {
    next();
  }
}
function guardMyRout(to, from, next) {
  fetch("/auth_config/").then((result) => {
    return result.json().then((data) => {
      localStorage.setItem("isAuth", data);
    });
  });
  console.log(localStorage.getItem("isAuth"));
  if (localStorage.isAuth) {
    next();
  } else {
    next("/");
  }
}

Vue.use(VueRouter);
const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/shop_by_category",
    name: "categoryList",
    component: categoryList,
  },
  {
    path: "/products",
    name: "productList",
    component: productList,
  },
  {
    path: "/cart",
    name: "AddToCart",
    component: AddToCart,
    beforeEnter: guardMyRout,
  },
  {
    path: "/products/:id/:slug",
    name: "productDetails",
    component: productDetails,
  },
  {
    path: "/category/:id/:slug",
    name: "shopByCategory",
    component: shopByCategory,
  },
  {
    path: "/login",
    name: "login",
    component: login,
    beforeEnter: guardLogin,
  },
  {
    path: "/signup",
    name: "signup",
    component: signup,
  },
  {
    path: "/reset/password/",
    name: "forgetPassword",
    component: forgetPassword,
    beforeEnter: guardMyRout,
  },
  {
    path: "/profile/:slug",
    name: "profile",
    component: profile,
    beforeEnter: guardMyRout,
  },
  {
    path: "/search/:slug",
    name: "search",
    component: search,
  },
  {
    path: "/payment/success/",
    name: "success",
    component: success,
    beforeEnter: guardMyRout,
  },
  {
    path: "/payment/cancelled/",
    name: "cancel",
    component: cancel,
    beforeEnter: guardMyRout,
  },
  {
    path: "*",
    name: "404",
    component: NotFound,
  },
];

const router = new VueRouter({
  mode: "history",
  // base: __dirname,
  routes,
});

router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some((record) => record.meta.requiresAuth);

  if (requiresAuth && !localStorage.getItem("isAuth")) next("login");
  else next();
});

export default router;
