<template>
  <div class="main">
    <section class="ftco-section">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-md-5 col-lg-5">
            <div class="mt-5">
              <form @submit.prevent="login()" method="post" class="profile p-5">
                <h3 class="mb-4 text-center">Login</h3>
                <div class="form-group">
                  <input
                    type="text"
                    class="profile w-100"
                    :class="Wvalid"
                    placeholder="Username"
                    v-model="username"
                    required
                  />
                  <div class="invalidtooltip" :class="display">
                    Username Or Password is Incorrect
                  </div>
                </div>
                <div class="form-group">
                  <input
                    id="password-field"
                    type="password"
                    class="profile w-100"
                    :class="Wvalid"
                    placeholder="Password"
                    v-model="password"
                    required
                  />
                </div>
                <div class="form-group">
                  <button
                    type="submit"
                    class="profile btn-success submit px-3 w-100"
                  >
                    Login
                  </button>
                </div>
                <div class="form-group">
                  <div class="float-left">
                    <router-link to="reset/password/"
                      >Forgot Password</router-link
                    >
                  </div>
                  <div class="float-rightt">
                    <label class=""
                      >Remember Me
                      <input type="checkbox" checked />
                    </label>
                  </div>
                </div>
              </form>
              <p class="w-100 text-center mt-4">
                &mdash; Or Sign In With &mdash;
              </p>
              <div class="social text-center">
                <a href="#" class="px-2 py-2 mr-md-1 rounded"
                  ><span class="ion-logo-facebook mr-2"></span> Facebook</a
                >
                <a href="#" class="px-2 py-2 ml-md-1 rounded"
                  ><span class="ion-logo-twitter mr-2"></span> Twitter</a
                >
              </div>
              <p class="w-100 text-center mt-4">
                &mdash; New With Us Create Un Account ?
                <router-link to="signup">Sign up</router-link> &mdash;
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "login",
  data() {
    return {
      username: "",
      password: "",
      Wvalid: "",
      display: "",
    };
  },
  methods: {
    login() {
      axios
        .post("http://vuengo.herokuapp.com/auth/login/", {
          csrfmiddlewaretoken: window.$cookies.get("csrftoken"),
          username: this.username,
          password: this.password,
        })
        .then((response) => {
          fetch("/auth_config/").then((result) => {
            return result.json().then((data) => {
              localStorage.setItem("isAuth", data);
            });
          });
          console.log(response.data);
          this.$store.state.isAuth = true;
          this.user();
          this.cart();
          this.$router.push({ name: "Home" });
        })
        .catch((error) => {
          this.Wvalid = "";
          console.log(error.response.data);
          this.display = "d-block";
          this.Wvalid = "invalid";
        });
    },
    user() {
      this.$store.dispatch("getUser");
    },
    cart() {
      this.$store.dispatch("getCart");
    },
    getAuth() {
      this.$store.dispatch("getAuth");
    },
  },
};
</script>
