import axios from "axios";

let baseUrl = "http://vuengo.herokuapp.com/data/products/";
let featuredUrl =
  "http://vuengo.herokuapp.com/data/products/?category=&min_price=&max_price=&featured=true";
let categoriesUrl = "http://vuengo.herokuapp.com/data/Categories/";
let UserUrl = "http://vuengo.herokuapp.com/data/users/";

export const items = [];
export const featuredProducts = [];
export const AllCategories = [];
export const AllUsers = [];
export let Cart = [];

// Get All Users
axios
  .get(UserUrl)
  .then((response) => {
    for (const user of response.data) {
      AllUsers.push(user);
    }
  })
  .catch((response) => {
    console.log(response);
  });
// Get All Products
axios
  .get(baseUrl)
  .then((response) => {
    for (const item of response.data) {
      if (item.price) {
        items.push(item);
      }
    }
  })
  .catch((response) => {
    console.log(response);
  });
// Get Featured Products
axios
  .get(featuredUrl)
  .then((response) => {
    for (const item of response.data) {
      if (item.price) {
        featuredProducts.push(item);
      }
    }
  })
  .catch((response) => {
    console.log(response);
  });
// Get All Categories
axios
  .get(categoriesUrl)
  .then((res) => {
    for (const category of res.data) {
      AllCategories.push(category);
    }
  })
  .catch((e) => {
    console.log(e);
  });

